.overlay {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1010;
    background: rgba(0, 0, 0, .6);
    cursor: pointer;
}

.dialog {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1020;

    box-sizing: border-box;
    margin: 0 auto;

    width: 720px;
    max-width: 100% !important;
    padding: 30px 30px;

    background: #1C1C21;

    box-shadow: 0 24px 32px rgba(0, 0, 0, 0.04), 0 16px 24px rgba(0, 0, 0, 0.04), 0 4px 8px rgba(0, 0, 0, 0.04), 0 0 1px rgba(0, 0, 0, 0.04);
    border-radius: 40px;
}

.header {
    display: flex;
    padding: 10px 15px;
    margin: calc(var(--offset-base-size) * 2);
    font-family: 'Jet Brains Mono';
    font-weight: bold;
    font-size: 24px;
    line-height: 1.25;
}

.close {
    position: absolute;
    top: 35px;
    right: 35px;
}

.body {
    clear: both;
}